.App {
    color: black;
    text-align: center;
}

.title.app {
    font-size: 50px;
}

.desc {
    color: dimgray;
}

.desc.large {
    color: dimgray;
    font-size: 25px;
}

.anchor {
    background-color: black;
    border-radius: 15px;
    padding: 15px;
    color: white;
    text-decoration: none;
    transition: 0.3s ease-out;
}

.anchor:hover {
    color: lightblue;
    padding: 18px;
}

.cursor {
    animation: blink 1s;
    animation-iteration-count: infinite;
  }
  
  @keyframes blink {
    0% {
      opacity: 0;
    }
  
    50% {
      opacity: 0;
    }
  
    75% {
      opacity: 1;
    }
  
    80% {
      opacity: 1;
    }
  
    95% {
      opacity: 1;
    }
  
    100% {
      opacity: 0;
    }
  }